<template lang="html">
  <svg class="hd-svg-send" :width="sizeUnit" :height="sizeUnit" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path :fill="color" d="M0 1024l106.496-474.112 588.8-36.864-588.8-39.936-106.496-473.088 1024 512z"  /></svg>
</template>

<script>
export default {
  name: 'hd-svg-send',
  props: {
    size: {
      type: [String, Number],
      default: 0.5
    },
    unit: {
      type: String,
      default: 'rem'
    },
    color: {
      type: String,
      default: '#666'
    }
  },
  computed: {
    sizeUnit () {
      return this.size + this.unit
    }
  }
}
</script>

<style lang="pcss">
</style>



